<template>
  <div class="hello">
    <template v-if="!big">
      <img class="img" :src="src" />
      <button style="height: 50px;width: 50px;margin: 20px;" @click="big = !big">meow</button>
      <button style="height: 50px;width: 50px" @click="meow">meow</button>
    </template>
    <template v-else>
      <img class="big-img" :src="src" />
      <button style="height: 50px;width: 50px;margin: 20px;" @click="big = !big">meow</button>
      <button style="height: 50px;width: 50px" @click="meow">meow</button>
    </template>
  </div>
</template>

<script>
import cos from 'cos-js-sdk-v5';
import list from './list';

export default {
  name: 'HelloWorld',
  props: ['sid', 'skey', 'password', 'prefix', 'meowbreak'],
  data() {
    return {
      src: '',
      cos: null,
      big: false,
    }
  },
  mounted() {
    this.cos = new cos({
      SecretId: this.sid,
      SecretKey: this.skey,
    });
    this.meow();
  },
  methods: {
    getOne() {
      const length = list.length;
      return list[(Math.floor(Math.random() * length))];
    },
    meow() {
      this.cos.getObject({
        Bucket: 'zerohime-1259548971', /* 填入您自己的存储桶，必须字段 */
        Region: 'ap-guangzhou',  /* 存储桶所在地域，例如ap-beijing，必须字段 */
        Key: this.getOne(),  /* 存储在桶里的对象键（例如1.jpg，a/b/test.txt），必须字段 */
      }).then(({ Body }) => {
        this.src = `${this.prefix}${this.meowbreak(Body, this.password)}`;
      });
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
